import {Component, Inject, OnInit} from '@angular/core'
import {SummaryService} from '../summary.service'
import * as moment from 'moment'
import {MatDialog} from '@angular/material'
// import {FlatpickrOptions} from 'ng2-flatpickr'
import Russian from 'flatpickr/dist/l10n/zh.js'
import {Router} from '@angular/router'
import {LocalStorage} from '../../core/services/localstorage.service'
import appAlert from '../../utils/alert'
import appUtil from '../../utils/pro-util'
import {ConnectionService} from '../../core/services/connection.service'


@Component({
  selector: 'app-summary-config',
  templateUrl: 'summary-config.component.html',
  styleUrls: ['summary-config.component.less']
})

export class SummaryConfigComponent implements OnInit {
  targetList: any[] = []
  params: any = {pageSize: 20, currentPageNo: 1}
  totalCount = 0
  loadingIndicator = true
  isPermission// 是否有权限
  isSpinner// 加载进度
  optBtns

  constructor(private service: SummaryService,
              private router: Router,
              private _dialog: MatDialog,
              private connectionService: ConnectionService,
              // public dialogRef: MatDialogRef<PhoneNoteComponent>
            ) {
  }

  ngOnInit() {
    this.optBtns = [{value: 0, name: '修改'}, {value: 1, name: '删除'}]
  }

  /**加载 拨测任务执行 列表**/
  reload() {
    this.isSpinner = 1
    this.loadingIndicator = true
    this.service.reloadConfig(appUtil.filterParams(this.params))
      .then(res => {
        this.totalCount = res.data.totalCount
        if (res.data.status === 0) {
            if (res.data.permission === -1) {
              this.isPermission = 0
            } else {
              this.isPermission = 1
              this.isSpinner = 0
                    this.loadingIndicator = false
                    this.targetList = res.data.result
            }
        } else {
          this.connectionService.isLoginByResult(res.data.msg)
        }
    })
    .catch(err => {
      appAlert.common.actionFailed('请求"专线配置"')
    })
  }

  getLimit(event) {
    Object.assign(this.params, {currentPageNo: 1, pageSize: event})
    this.reload()
  }


  /**操作按钮**/
  operateButton(value, row, rowIndex) {
    switch (value) {
      case 1: {
        this.router.navigate(['/index/calltask/call-result'])
        LocalStorage.set('taskExecuteId', row.id)
        LocalStorage.set('taskId', row.taskId)
        break
      }
      case 1: {
        console.log(row.id)
        appAlert.common.remove('数据', () => {
          this.service.deleteConfig(row.id)
            .then(res => {
              if (res.data.status === 0) {
                  appAlert.common.actionSuccess('删除拨测任务执行')
                  this.reload()
              } else {
                appAlert.login.failed(res.data.msg || '删除拨测任务执行')
              }
            })
        })
        break
      }
    }
  }
}
