import {Component, ElementRef, Inject, OnInit} from '@angular/core'
import {MAT_DIALOG_DATA, MatDialogConfig, MatDialogRef} from '@angular/material'
import {DomSanitizer, SafeHtml} from '@angular/platform-browser'
import appAlert from '../utils/alert'

@Component({
  selector: 'app-detail-dialog',
  template: `
      <div class="redefine-dialog ">
          <h1 class="dialog-head">{{data.title ? data.title : '详情'}}</h1>
          <div id="detail" class="dialog-content" [innerHtml]="sanitize(detail)"></div>
          <div>
              <button mat-button (click)="copyDetail()" class="sure">复制</button>
          </div>
      </div>
  `,
  styles: [`
    .sure{
        color: #fff;
        background: #0097d4;
    }
  `]
})
export class DetailDialogComponent implements OnInit {
  public static config: MatDialogConfig = {
    disableClose: false,
    width: '650px',
    minHeight: '200px',
    data: {}
  }
  errorArr: any = []
  detail: any

  constructor(public dialogRef: MatDialogRef<DetailDialogComponent>,
              private sanitizer: DomSanitizer,
              private elementRef: ElementRef,
              @Inject(MAT_DIALOG_DATA) public data: any) {
  }

  ngOnInit() {
    this.detail = JSON.stringify(this.data.detail, null, 2)
  }

  public sanitize(html: string): SafeHtml {
    return this.sanitizer.bypassSecurityTrustHtml('<pre>' + html + '</pre>')
  }

  copyDetail() {
    const copyHttp = this.elementRef.nativeElement.querySelector('#detail')
    const range = document.createRange()
    range.selectNode(copyHttp)
    window.getSelection().removeAllRanges()
    window.getSelection().addRange(range)
    document.execCommand('copy')
    appAlert.login.success('复制成功！')
  }
}
