import {MatSnackBar} from '@angular/material'
import {Injectable} from '@angular/core'
import {SnackBarComponent} from '../../shared/snackBar.component'

@Injectable()
export class SnackBarService {
  constructor(private snackBar: MatSnackBar) {
  }

  success(title) {
    this.snackBarSetting(title, 'success', 'success-snack')
  }

  warning(title) {
    this.snackBarSetting(title, 'warning', 'warning-snack')
  }

  error(title) {
    this.snackBarSetting(title, 'error', 'error-snack')
  }

  snackBarSetting(title, type, css, duration = 2000) {
    this.snackBar.openFromComponent(SnackBarComponent,
      {data: {title: title, type: type}, verticalPosition: 'top', panelClass: [css], duration: duration})
  }

}
