import {Injectable} from '@angular/core'
import axios from 'axios'

import {ConstantService} from './constant.service'
import {LocalStorage} from './localstorage.service'
import appAlert from '../../utils/alert'
import {Router} from '@angular/router'

@Injectable()
export class ConnectionService {
  public isLogin: boolean
  private instance = axios.create({
    baseURL: ConstantService.HOST,
    withCredentials: true,
    headers: {},
    validateStatus: (status) => {
      return status >= 200 && status < 500 // default
    },
  })
  agentColStorage: any

  constructor(private router: Router) {
    this.isLogin = true
    this.instance.interceptors.response.use(function (response) {
      // 对返回的数据进行一些处理
      if ([401, 403].includes(response.status)) {
        response.data = {}
        response.data.msg = '没有访问权限！'
      }
      return response
    }, function (error) {
      // 对返回的错误进行一些处理
      return Promise.reject(error)
    })
  }
  /**
   * @desc 获取指定的资源（base_url/resource?name='xxx'）
   * @param string path - 资源定位器
   * @param params - 资源定位器
   */
  get(path: string, params?: any) {
    if (params) {
      return this.instance.get(path, params)
    } else {
      return this.instance.get(path)
    }
  }

  /**
   * @desc 修改指定id的资源（base_url/resource/id）
   * @param string path - 资源定位器
   * @param data - 资源定位器
   */
  put(path: string, data: any) {
    return this.instance.put(path, data)
  }

  /**
   * @desc 删除指定id的资源（base_url/resource/id）
   * @param string path - 资源定位器
   */
  delete(path: string, data?: any) {
    if (data) {
      return this.instance.delete(path, data)
    } else {
      return this.instance.delete(path)
    }
  }

  /**
   * @desc 保存指定的资源（base_url/resource）
   * @param string path - 资源定位器
   * @param data - 资源定位器
   */
  post(path: string, data?: any) {
    if (data) {
      return this.instance.post(path, data)
    } else {
      return this.instance.post(path)
    }
  }

  login() {
    this.isLogin = true
  }

  logout() {
    this.isLogin = false
  }

  /**根据返回的错误提示，判断是否未登录**/
  // isLoginByResult(msg, note) {
  //   if (msg && msg.includes('未登录')) {
  //     this.logout()
  //     this.router.navigate(['/signin'])
  //     localStorage.clear()
  //   } else {
  //     // appAlert.common.actionFailed(`请求"${note}"`)
  //     appAlert.login.failed(msg || `请求"${note}"失败，请稍后重试!`)
  //   }
  // }

  isLoginByResult(err) {
    if (err.data.status === 401) {
      this.logout()
      const user = LocalStorage.get('user')
      this.router.navigate(['/signin'])
      // this.saveAgentColumsShow()
    } else {
      appAlert.login.failed('网络连接不可用！')
    }
  }

  /**
   * @desc 拼接参数object
   * @param Object params - 参数，eg：{max:10, offset:0}
   * @return 将对象转换为查询参数 eg：max=10&offset=0
   */
  public getQueryString(params: any) {
    const keys = Object.keys(params)
    const queryStringArr: any = []
    for (let i = keys.length; i; i--) {
      const key = keys[i - 1]
      const value = params[key]
      if (typeof value === 'object') {
        const subKeys = Object.keys(value)
        for (let j = subKeys.length; j; j--) {
          const subKey = subKeys[j - 1]
          queryStringArr.push(`${key}[${subKey}]=${value[subKey]}`)
        }
      } else {
        queryStringArr.push(`${key}=${params[key]}`)
      }
    }
    return queryStringArr.join('&')
  }

  /**创建a标签，实现点击下载**/
  createA(url, filename) {
    const eleLink: any = document.createElement('a')
    eleLink.download = filename
    eleLink.style.display = 'none'
    eleLink.href = url
    document.body.appendChild(eleLink)
    eleLink.click()
    document.body.removeChild(eleLink)
  }
    /**清除localStorage时，需保留agentColumsShow**/
    // saveAgentColumsShow() {
    //   this.agentColStorage = Object.assign({}, LocalStorage.get('agentColumsShow'))
    //   localStorage.clear()
    //   LocalStorage.set('agentColumsShow', this.agentColStorage)
    // }
}
