import {Injectable} from '@angular/core'
import {action, observable} from 'mobx-angular'
import {ConnectionService} from './connection.service'
import {ConstantService} from './constant.service'
import {LocalStorage} from './localstorage.service'
import {Subject} from 'rxjs'

@Injectable()
export class DataService {
  @observable menuShrinkageOffset: any = {}
  @observable showHead: any = {}
  @observable navTabs: any = []
  navTabsBottom: any = []
  @observable user: any = LocalStorage.get('user') || {}
  changeUserSubject
  shrinkChangedSubject
  // menuShrinkageOffset
  permissions: any = ['all']
  // shangeMenuSubject
  constructor(private connectionService: ConnectionService) {
    this.changeUserSubject = new Subject()
    this.shrinkChangedSubject = new Subject()
    // this.shangeMenuSubject = new Subject()
    // this.menuShrinkageOffset = new Subject()
  }

  /**
   * @desc 监控左侧菜单收展状态
   * @param left - 左侧菜单宽度
   */
  @action changeLeft(left) {
    const mleft = left + 'px'
    const sleft = 180 + left - 20 + 'px'
    const leftArr = {mleft: mleft, sleft: sleft}
    Object.assign(this.menuShrinkageOffset, leftArr)

  }

  @action changeQuery(queryName) {
    Object.assign(this.showHead, queryName)
  }

  // /**
  //  * @desc 点击配置菜单请求
  //  */
  // getLoginConsole() {
  //   const path = ConstantService.version + '/monitor/api/loginConsole'
  //   return this.connectionService.get(path)
  // }

  // /**
  //  * @desc 点击管理菜单请求
  //  */
  // getLoginReport() {
  //   const path = ConstantService.version + '/monitor/api/loginReport'
  //   return this.connectionService.get(path)
  // }

  @action
  changeSideBar() {
    const user: any = LocalStorage.get('user') || {}
    for (const key in this.user) {
      if (key) {
        delete this.user[key]
      }
    }
    Object.assign(this.user, user)
    this.navTabs.length = 0
    this.navTabs.push(...ConstantService.navTabs)
    // this.navTabsBottom.length = 0
    // this.shangeMenuSubject.next(this.navTabsBottom)
  }

  /**
   * @desc 判断一级菜单是否可以展开
   * */
  hasPermission(permissions, mark) {
    if (!mark || mark.length === 0) {
      return true
    } else {
      let i = 0, has = false
      const len = mark.length
      for (; i < len; i++) {
        if (permissions.includes(mark[i])) {
          has = true
          break
        }
      }
      return has
    }
  }

  resizeWindow() {
    setTimeout(() => {
      window.dispatchEvent(new Event('resize'))
    }, 250)
  }

}
