import { Component, OnInit } from '@angular/core'
import { Router } from '@angular/router'
// import 'rxjs/add/operator/toPromise'
import { SwalService } from '../../core/services/swal.service'
import { ConnectionService } from '../../core/services/connection.service'
import { LocalStorage } from '../../core/services/localstorage.service'
import { Md5 } from 'ts-md5/dist/md5'
import { ConstantService } from '../../core/services/constant.service'
@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.less']
})

export class LoginComponent implements OnInit {
    codePath
    user: any = {}
    isClick = false
    isWriteObj: any = {}

    constructor(public router: Router,
        private connectionService: ConnectionService,
        private swalService: SwalService
    ) {

    }

    ngOnInit() {
        this.getCode()
    }

    login(model: any) {
        this.isClick = true
        const path = '/interface' + ConstantService.version + '/system/login'
        const pass = model.password
        const params: any = {}
        params.userName = model.username
        params.securityCode = model.securityCode
        const authcode = params.userName + pass + params.securityCode
        params.authCode = Md5.hashStr(authcode).toString()
        LocalStorage.remove('user')
        this.connectionService.get(path, {params: params})
            .then(res => {
                if (res.data.status === 200) {
                    this.connectionService.login()
                    this.router.navigate(['/index'])
                    if (res.data.result) {
                        const user = res.data.result
                        console.log(user)
                        LocalStorage.set('user', user)
                    } else { // 200：已登录状态，请先登出
                        this.swalService.hint('error', res.data.msg)
                    }
                } else {
                    this.swalService.hint('error', res.data.msg)
                }
            })
            .catch(err => {
                this.swalService.hint('error', '网络错误')
                this.getCode()
            })
    }

    /**
     * @desc 点击验证码后重新获取验证码
     */
    getCode() {
        // this.codePath = '/interface' + ConstantService.version + '/system/getSecurityCodeImage' // *****上传到线上时使用
        this.codePath = ConstantService.HOST + '/interface' +  ConstantService.version + '/system/getSecurityCodeImage'
    }

    handler() {

    }

    trimCode() { // 按钮禁用的情况
        let code
        if (this.user.securityCode) {
            code = this.user.securityCode.replace(/\s/g, '')
        }
        if (code && code.length > 0) {
            return false
        } else {
            return true
        }
    }
    /**
     * @desc 判断是否输入,当松开按键的时候触发该事件
     */
    isWrite(value) {
        this.isWriteObj[value] = true
    }

}

