import {Component, enableProdMode, Input, OnChanges} from '@angular/core'
// import * as OrgChart from 'orgchart/dist/js/jquery.orgchart.js'
// require('html2canvas/dist/html2canvas.min.js')
declare let jQuery: any
// declare let require:any
enableProdMode()

@Component({
  selector: 'app-orgchart',
  template: `
      <div id="orgchart" style="margin-top: 30px"></div>
  `,
})

export class OrgChartComponent implements OnChanges {
  @Input() chartContanier: any
  @Input() chartData: any
  @Input() direction: any

  // 每当输入属性发生变化时就会调用，但位于处理内容（ng-content）或子视图之前。
  ngOnChanges(event) {// event---@input的6个
    this.initChart()
  }

  initChart() {
    jQuery('#orgchart').orgchart({
      'data': this.chartData,
      'nodeContent': 'value',
      'direction': this.direction,
      'exportButton': true,
      'exportFilename': 'GatewayDetail',
    })
  }
}

