import {Component, OnInit} from '@angular/core'
import {Router} from '@angular/router'

import {SwalService} from '../services/swal.service'
import {LocalStorage} from '../services/localstorage.service'
import {ConnectionService} from '../services/connection.service'
import {DataService} from '../services/data.service'
import {ConstantService} from '../services/constant.service'

@Component({
  selector: 'app-header',
  templateUrl: 'header.component.html',
  styleUrls: ['header.component.less'],
})

export class HeaderComponent implements OnInit {
  user: any = LocalStorage.get('user')
  showHead // 页面显示的变量
  isSupportList = false
  constructor(
    private router: Router,
    private connectionService: ConnectionService,
    private swalService: SwalService,
    private dataService: DataService
  ) {
    // this.user = this.dataService.user
    this.showHead = this.dataService.showHead // {tenancyName:,organizeName:,departName:,enterpriseId:}
  }

  ngOnInit() {
  }

  exit() {
    const path = '/interface' + ConstantService.version + '/system/logout'
    this.connectionService.post(path)
      .then(res => {
        if (res.data.status === 200) {
          this.dataService.showHead = {} // 退出时，清空存放"右上角"和"首页"显示的容器
          this.connectionService.logout()
          this.router.navigate(['/signin'])
        }
      }).catch(error => {
        this.swalService.hint('error', '退出登录失败，请重试！')
      })
  }
  showListSupport() {
    this.isSupportList = !this.isSupportList
  }
}


