import {Component, Inject} from '@angular/core'
import {MAT_SNACK_BAR_DATA} from '@angular/material'

@Component({
  selector: 'app-snackBar',
  template: `<div>
    <div class="iconBg">
      <mat-icon *ngIf="data.type === 'success'">done</mat-icon>
      <mat-icon *ngIf="data.type === 'error'">close</mat-icon>
      <mat-icon *ngIf="data.type === 'warning'">warning</mat-icon>
    </div>
    <div class="snack-contnt">{{data.title}}</div>
  </div>`,
  styles: [`
    .iconBg mat-icon{
      height: 40px;
      line-height: 40px;
      color: #fff;
      font-size: 20px;
    }
    .snack-contnt{
      padding: 0 10px 0 15px;
      height: 40px;
      display: block;
      overflow: hidden;
      font-size: 13px;
      line-height: 40px;
      margin-top: -40px;
      margin-left: 40px;
    }
  `]
})

export class SnackBarComponent {
  constructor(@Inject(MAT_SNACK_BAR_DATA) public data: any) {
  }
}
