import {NgModule} from '@angular/core'
import {RouterModule, Routes} from '@angular/router'
import {IndexComponent} from './core/index.component'
import { LoginComponent } from './clientauth/login/login.component'
import {AuthGuard} from './core/services/auth-guard.service'
import {PageNotFoundComponent} from './core/page-not-found.component'
// import {AuthorResolver} from './resolver/author.resolver'
import {RouterComponent} from './core/router.component'
import {HomeComponent} from './core/home/home.component'
import {SummaryConfigComponent} from './summary/config/summary-config.component'

// 配置路由信息
const routes: Routes = [
  {path: '', redirectTo: 'signin', pathMatch: 'full'},
  {path: 'signin', component: LoginComponent},
  {
    path: 'index', component: IndexComponent,
    canActivate: [AuthGuard],
    // resolve: {author: AuthorResolver},
    children: [
      {path: '', redirectTo: 'home', pathMatch: 'full'},
      {path: 'home', component: HomeComponent},
      {
        path: 'summary', component: RouterComponent,
        children: [
          {path: '', redirectTo: 'configuration', pathMatch: 'full'},
          {path: 'configuration', component: SummaryConfigComponent},
          // {path: 'task-result', component: TaskResultComponent},
          // {path: 'call-result', component: CallResultComponent}
        ]
      }
    ]},
  {path: '**', component: PageNotFoundComponent}
]

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
    RouterModule
  ],
  providers: [
    // AuthorResolver
  ]
})

export class AppRoutingModule {
}
